import React, { useState, useEffect } from "react";
import moment from "moment";

import Toolbar from "../Toolbar";
import EventFrom from "../EventForm";
import Service from "../../services";
import { fetchProjects, updateProject } from "../../services/api";

import "dhtmlx-scheduler";
import "dhtmlx-scheduler/dhtmlxscheduler_material.css";
import "./Scheduler.css";

const scheduler = window.scheduler;

const time_step = {
  week_timeline: 1440,
  day_timeline: 1440,
  halfday_timeline: 720,
};

export default function Scheduler({ parameters }) {
  const [mode, setMode] = useState("day_timeline");
  const [events, setEvents] = useState([]);
  const [open, setOpen] = useState(false);
  const [event, setEvent] = useState(null);
  const [employees, setEmployees] = useState([]);
  const [sections, setSections] = useState([]);
  const [projects, setProjects] = useState([]);
  const [weeklyPlanning, setWeeklyPlanning] = useState([]);
  const [dates, setDates] = useState({
    date: moment().format("YYYY-MM-DD"),
    toDate: moment().format("YYYY-MM-DD"),
  });
  const { id } = parameters;

  const getHours = React.useCallback(
    ({ date, toDate }, hours) => {
      if (["day_timeline", "month_timeline"].includes(mode)) {
        return (
          Number(hours) /
          Number(
            moment(toDate).diff(
              moment(date),
              mode === "day_timeline" ? "days" : "months"
            )
          )
        );
      }
    },
    [mode]
  );

  const mapPlanningTimes = React.useCallback(
    (events) => {
      if (!events || !events?.length) return [];
      return events?.map((event) => {
        const { date, toDate, fullName, employee, project, plannedHours } =
          event || {};
        return {
          ...event,
          start_date: date,
          end_date: toDate,
          text: fullName,
          section_id: `${employee && employee.id}_${project && project.id}`,
          hours: getHours({ date, toDate }, plannedHours),
        };
      });
    },
    [getHours]
  );

  const addProjectLines = React.useCallback(
    async ({
      plannedHours,
      project,
      employee,
      product,
      projectTask,
      description,
      id,
      version,
      start_date,
      end_date,
      startOnSelect,
      endOnSelect,
    } = {}) => {
      let paylod = {
        date: moment(start_date).format("YYYY-MM-DD"),
        toDate: moment(end_date).format("YYYY-MM-DD"),
        plannedHours,
        startOnSelect: startOnSelect?.value,
        endOnSelect: endOnSelect?.value,
        employee,
        project: { id: project?.id },
        product: product?.id ? { id: product?.id } : null,
        projectTask: projectTask?.id ? { id: projectTask?.id } : null,
        description,
      };
      if (version >= 0) {
        paylod.version = version;
        paylod.id = id;
      }
      const res = await Service.add(
        "com.axelor.apps.project.db.ProjectPlanningTime",
        paylod
      );
      if (res.status === -1) {
        setEvents((events = []) => events.filter((e) => e.id !== event.id));
      } else {
        const updatedEvent = (res?.data && res.data[0]) || {};
        setEvents((events = []) =>
          events.map((e) => (e.id === id ? { ...e, ...updatedEvent } : e))
        );
      }
    },
    [event]
  );

  const handleClose = () => {
    if (event?.version >= 0) {
      setOpen(false);
      return;
    }
    setEvents((events) => (events || []).filter((e) => e.id !== event.id));
    setOpen(false);
  };

  const handleDelete = async () => {
    const res = await Service.delete(
      "com.axelor.apps.project.db.ProjectPlanningTime",
      event?.id
    );
    if (res.status === 0) {
      setEvents((events) => (events || []).filter((e) => e.id !== event.id));
    }
    setOpen(false);
  };

  const handleOk = () => {
    if (!event?.project) {
      window.alert("Project is required");
      return;
    }
    if (!event.plannedHours) {
      window.alert("Hours are required");
      return;
    }
    if (!event.product) {
      window.alert("Activity is required");
      return;
    }
    setOpen(false);
    setEvents((events = []) => {
      return [
        ...events,
        {
          ...event,
          text: event?.text || event?.project?.fullName,
        },
      ];
    });
    addProjectLines(event);
  };

  const getPlannedHours = React.useCallback(
    ({ start_date, end_date }, hours) => {
      if (["day_timeline", "month_timeline"].includes(mode)) {
        return (
          Number(hours) *
          moment(end_date).diff(
            moment(start_date),
            mode === "day_timeline" ? "days" : "months"
          )
        );
      }
    },
    [mode]
  );

  const handleEvent = (name, value) => {
    if (["hours", "end_date", "start_date"].includes(name)) {
      setEvent((event) => {
        const newEvent = { ...event, [name]: value };
        return {
          ...newEvent,
          plannedHours: ["end_date", "start_date"].includes(name)
            ? getPlannedHours(newEvent, newEvent?.hours)
            : getPlannedHours(newEvent, value),
        };
      });
      return;
    }
    setEvent((event) => {
      return { ...event, [name]: value };
    });
  };

  const formatDate = (date) => {
    return moment(date).format("YYYY-MM-DD");
  };

  const addSpans = React.useCallback(
    (plans) => {
      let sectionIds = [];
      sections.forEach((element) => {
        const { children } = element;
        sectionIds = [...sectionIds, ...children.map((c) => c.key)];
      });
      sectionIds.forEach((id) => {
        const ids = id?.split("_");
        const holidayConfig = {
          days: [...(plans[ids[0]].holidays.map((w) => new Date(w)) || [])],
          css: "holiday_section",
          zones: "fullday",
          type: "dhx_time_block",
          sections: {
            day_timeline: id,
            // week_timeline: id,
            month_timeline: id,
            halfday_timeline: id,
          },
        };
        scheduler.deleteMarkedTimespan(holidayConfig);
        scheduler.addMarkedTimespan(holidayConfig);
      });
      scheduler.updateView();
    },
    [sections]
  );

  const getDaysBetweenDates = React.useCallback(
    (weekends) => {
      const { date, toDate } = dates;
      let startDate = moment(date);
      let endDate = moment(toDate);

      let now = startDate.clone(),
        newDates = [];

      while (now.isSameOrBefore(endDate)) {
        if (weekends.includes(now.day())) {
          newDates.push(new Date(now));
        }
        now.add(1, "days");
      }
      return newDates;
    },
    [dates]
  );

  const addMember = async (record) => {
    await updateProject(record);
  };

  /**
   *
   * @param {*} section
   * If section(employee) is available check for children(projects).
   *  Add new projects
   *    If project is available keep as it is
   *    else add new project and update memberUserSet in project
   * else
   *  Add new employee with projects (Recursive project logic)
   */
  const addNewSection = (section) => {
    const { employee, projects } = section || {};
    const projectIds = projects?.map((p) => p.id);
    const isSection = sections?.find((s) => s.id === employee?.id);
    setEmployees((employees) => [...(employees || []), employee]);
    if (isSection) {
      const { children } = isSection || {};
      const childrenIds = children.map((c) => c.id);
      const addIds = projectIds.filter((p) => !childrenIds.includes(p));
      setSections((sections = []) =>
        sections.map((section) =>
          section?.id === employee?.id
            ? {
                ...section,
                children: [
                  ...section.children,
                  ...(addIds?.map((id) => {
                    const proj = projects.find((p) => p.id === id);
                    if (
                      proj.membersUserSet.find(
                        (m) => m.id !== employee?.user?.id
                      )
                    ) {
                      addMember({
                        ...proj,
                        membersUserSet: [...proj.membersUserSet, employee.user],
                      });
                      return {
                        ...proj,
                        key: `${employee.id}_${proj.id}`,
                        label: proj.fullName,
                      };
                    }
                    return {
                      ...proj,
                      key: `${employee.id}_${proj.id}`,
                      label: proj.fullName,
                    };
                  }) || []),
                ],
              }
            : { ...section }
        )
      );
    } else {
      setSections((sections) => [
        ...sections,
        {
          ...employee,
          key: employee.id,
          label: employee.name,
          open: true,
          children: [...(projects || [])]?.map((p) => {
            if (
              projects?.find((proj) =>
                (proj.membersUserSet || [])
                  .map((u) => u.id)
                  .includes(employee.user?.id)
              )
            ) {
              return { ...p, key: `${employee.id}_${p.id}`, label: p.fullName };
            } else {
              addMember({
                ...p,
                membersUserSet: [...p.membersUserSet, employee.user],
              });
              return { ...p, key: `${employee.id}_${p.id}`, label: p.fullName };
            }
          }),
        },
      ]);
    }
  };

  useEffect(() => {
    if (!weeklyPlanning) return;
    let sectionIds = [];
    sections.forEach((element) => {
      const { children } = element;
      sectionIds = [...sectionIds, ...children.map((c) => c.key)];
    });
    sectionIds.forEach((id) => {
      const ids = id?.split("_");
      const employeeId = ids[0];
      const plan = weeklyPlanning[employeeId];
      const weekends = [];
      for (const [key, value] of Object.entries(plan || {})) {
        if (!Object.values(value).find((s) => s !== "0")) {
          weekends.push(Number(key));
        }
      }
      const days = getDaysBetweenDates(weekends) || [];
      const config = {
        days: [...days],
        css: "non_working_day_section",
        zones: "fullday",
        type: "dhx_time_block",
        sections: {
          day_timeline: id,
          // week_timeline: id,
          month_timeline: id,
          halfday_timeline: id,
        },
      };
      scheduler.deleteMarkedTimespan(config);
      scheduler.addMarkedTimespan(config);
      scheduler.updateView();
    });
  }, [weeklyPlanning, getDaysBetweenDates, sections]);

  useEffect(() => {
    (async () => {
      let minDate = scheduler.getState().min_date;
      let maxDate = scheduler.getState().max_date;
      if (!minDate || !maxDate) return;
      const res = await Service.search(
        "com.axelor.apps.project.db.ProjectPlanningTime",
        {
          data: {
            criteria: [
              {
                fieldName: "date",
                operator: ">=",
                value: minDate,
              },
              {
                fieldName: "date",
                operator: "<=",
                value: maxDate,
              },
            ],
          },
        }
      );
      setEvents(mapPlanningTimes(res?.data));
    })();
  }, [employees, mapPlanningTimes, mode]);

  useEffect(() => {
    /**
     * Update timeline size dynamically
     */
    const onBeforeViewChange = scheduler.attachEvent(
      "onBeforeViewChange",
      function (old_mode, old_date, mode, new_date) {
        let date = formatDate(moment(new_date).startOf("month"));
        let toDate = formatDate(moment(new_date).endOf("month"));
        if (mode === "week_timeline") {
          date = formatDate(moment(new_date).startOf("week"));
          toDate = formatDate(moment(new_date).endOf("week"));
        } else if (mode === "month_timeline") {
          date = formatDate(moment(new_date).startOf("year"));
          toDate = formatDate(moment(new_date).endOf("year"));
        }
        setDates({
          date,
          toDate,
        });
        setMode(mode);
        let day_timeline = scheduler.getView("day_timeline");
        let halfday_timeline = scheduler.getView("halfday_timeline");
        day_timeline.x_size = moment(date).daysInMonth();
        day_timeline.x_length = moment(date).daysInMonth();
        halfday_timeline.x_size = moment(date).daysInMonth() * 2;
        halfday_timeline.x_length = moment(date).daysInMonth() * 2;
        return true;
      }
    );

    const onViewChange = scheduler.attachEvent(
      "onViewChange",
      function (mode, date) {
        if (mode === "month_timeline") {
          scheduler.config.time_step =
            moment(date).daysInMonth() * time_step["day_timeline"];
        } else {
          scheduler.config.time_step = time_step[mode];
        }
      }
    );

    const onDragEnd = scheduler.attachEvent("onDragEnd", function (id) {
      let ev = scheduler.getEvent(id);
      const event = events.find((e) => e.id === ev.id);
      const ids = ev?.section_id?.split("_");
      let project = projects?.find((e) => e.id === Number(ids && ids[1]));
      let employee = employees?.find((e) => e.id === Number(ids && ids[0]));
      if (
        ev?.version >= 0 &&
        (Number(event?.hours) !== Number(ev.hours) ||
          project?.id !== event?.project?.id ||
          employee?.id !== event?.employee?.id)
      ) {
        addProjectLines({
          ...ev,
          plannedHours: getPlannedHours(
            { start_date: ev.start_date, end_date: ev.end_date },
            ev.hours
          ),
          project,
          employee,
        });
      }
    });

    const openView = (id) => {
      let ev = scheduler.getEvent(id);
      const startHour = moment(ev.start_date).hour();
      const endHour = moment(ev.end_date).hour();
      const ids = ev?.section_id?.split("_");
      setEvent({
        ...ev,
        startOnSelect:
          startHour === 12
            ? { label: "Afternoon", value: 1 }
            : { label: "Morning", value: 0 },
        endOnSelect:
          endHour === 12
            ? { label: "Morning", value: 0 }
            : { label: "Afternoon", value: 1 },
        project: projects?.find((e) => e.id === Number(ids && ids[1])),
        employee: employees?.find((e) => e.id === Number(ids && ids[0])),
      });
      setOpen(true);
    };

    const onClick = scheduler.attachEvent("onClick", function (id, e) {
      openView(id);
    });

    scheduler.showLightbox = (id) => {
      openView(id);
    };

    return () => {
      scheduler.detachEvent(onBeforeViewChange);
      scheduler.detachEvent(onViewChange);
      scheduler.detachEvent(onDragEnd);
      scheduler.detachEvent(onClick);
    };
  }, [employees, getPlannedHours, projects, events, addProjectLines]);

  useEffect(() => {
    scheduler.plugins({
      timeline: true,
      treetimeline: true,
      limit: true,
    });
    scheduler.locale.labels.day_timeline = "Day";
    // scheduler.locale.labels.week_timeline = "Week";
    scheduler.locale.labels.month_timeline = "Month";
    scheduler.locale.labels.halfday_timeline = "Half day";

    scheduler.locale.labels.section_custom = "Section";
    scheduler.config.details_on_create = true;
    scheduler.config.details_on_dblclick = true;
    scheduler.config.time_step = 1440;
    scheduler.xy.scale_height = 20;
    scheduler.config.hour_date = "%y-%m-%d %H:%i:%s";
    scheduler.config.check_limits = false;

    const commonProperties = {
      section_autoheight: false,
      y_unit: sections,
      folder_dy: 35,
      y_property: "section_id",
      dy: 60,
      render: "tree",
      cell_template: true,
    };
    scheduler.createTimelineView({
      name: "halfday_timeline",
      x_unit: "minute",
      x_date: "%A",
      x_step: 720,
      x_size: 62,
      x_start: 0,
      x_length: 62,
      ...commonProperties,
      second_scale: {
        x_unit: "day",
        x_date: "%D %j",
      },
    });

    scheduler.createTimelineView({
      name: "day_timeline",
      x_unit: "day",
      x_date: "%D %j", //%D<br><b>%j</b>
      days: 1,
      x_step: 1,
      x_start: 1,
      x_size: moment().daysInMonth(),
      x_length: moment().daysInMonth(),
      ...commonProperties,
    });

    // scheduler.createTimelineView({
    //   name: "week_timeline",
    //   days: 7,
    //   x_unit: "day",
    //   x_date: "%j %F",
    //   x_step: 1,
    //   x_size: 7,
    //   x_start: 0,
    //   x_length: 7,
    //   ...commonProperties,
    // });

    scheduler.createTimelineView({
      name: "month_timeline",
      x_unit: "month",
      x_date: "%F",
      x_step: 1,
      x_size: 12,
      x_start: 1,
      x_length: 12,
      ...commonProperties,
    });

    //For start date of view
    scheduler.date.halfday_timeline_start = function (date) {
      return new Date(moment(date).startOf("month"));
    };
    scheduler.date.day_timeline_start = function (date) {
      return new Date(moment(date).startOf("month"));
    };
    // scheduler.date.week_timeline_start = function (date) {
    //   return new Date(moment(date).startOf("week"));
    // };
    scheduler.date.month_timeline_start = function (date) {
      return new Date(moment(date).startOf("year"));
    };

    const cellValue = function (evs, date, section) {
      if (section.children) {
        const timeline = scheduler.getView();
        const events = timeline.selectEvents({
          section: section.key,
          date: date,
          selectNested: true,
        });
        const startDate =
          mode === "day_timeline"
            ? moment(date)
            : moment(date).startOf("month");
        const endDate =
          mode === "day_timeline" ? moment(date) : moment(date).endOf("month");

        function getTotalHours(empId, startDate, endDate, match = () => true) {
          if (!weeklyPlanning) return 0;
          const emp = weeklyPlanning[empId];
          const getEmpHr = (mm) => {
            const halfs = emp && emp[mm.day()];
            if (!halfs) return 0;
            return +(halfs[0] || 0) + +(halfs[1] || 0);
          };

          if (!(startDate instanceof moment)) {
            startDate = moment(startDate);
          }
          if (!(endDate instanceof moment)) {
            endDate = moment(endDate);
          }
          const $dd = startDate.clone();
          let total = 0;
          while ($dd.isSameOrBefore(endDate)) {
            if (match($dd)) {
              total += getEmpHr($dd);
            }
            $dd.add(1, "days");
          }
          return total;
        }

        function getCount() {
          if (!weeklyPlanning) return 0;
          return events?.reduce((accumulator, object) => {
            const { id } = object?.employee || {};
            const emp = weeklyPlanning[id];

            const objStartDate = moment(object.start_date);
            const objEndDate = moment(object.end_date).subtract(1, "days");
            const eventExpectedHours = getTotalHours(
              id,
              objStartDate,
              objEndDate
            );
            const totalHrs = (() => {
              if (mode === "month_timeline") {
                return getTotalHours(id, startDate, endDate, ($dd) =>
                  $dd.isBetween(objStartDate, objEndDate, undefined, "[]")
                );
              }
              const todayHours = emp && emp[moment(date).day()];
              if (!todayHours) return 0;
              return +(todayHours[0] || 0) + +(todayHours[1] || 0);
            })();
            const perHr = +(object.plannedHours || 0) / eventExpectedHours;
            const eventDayHr = perHr * totalHrs;
            return accumulator + eventDayHr;
          }, 0);
        }

        const availableHrs = getTotalHours(section.id, startDate, endDate);
        const workedHrs = getCount();
        const percentage = Math.round((workedHrs * 100) / availableHrs);
        let className = "load-marker-blue";
        if (percentage > 100) {
          className = "load-marker-red";
        } else if (percentage === 100) {
          className = "load-marker-green";
        }
        return `<div class='load-marker ${className}'>${percentage || 0}</div>`;
      }
      return "";
    };
    scheduler.templates.day_timeline_cell_value = cellValue;
    // scheduler.templates.week_timeline_cell_value = cellValue;
    scheduler.templates.month_timeline_cell_value = cellValue;
    scheduler.templates.halfday_timeline_cell_value = cellValue;

    scheduler.init("scheduler_here", new Date(), mode);
    scheduler.clearAll();
    scheduler.parse(events);
  }, [sections, events, mode, weeklyPlanning, employees]);

  useEffect(() => {
    (async () => {
      const employeeIds = employees?.map((emp) => emp.id);
      if (!employeeIds) return;
      const res = await Service.action({
        action: "com.axelor.apps.hr.web.EmployeeController:getDayPlanningHours",
        data: {
          employeeIds,
        },
      });
      setWeeklyPlanning(res?.data);
    })();
  }, [employees]);

  useEffect(() => {
    (async () => {
      const employeeIds = employees?.map((emp) => emp.id);
      if (!employeeIds && sections?.length) return;
      const res = await Service.action({
        action:
          "com.axelor.apps.hr.web.EmployeeController:getHolidayForEmployees",
        data: {
          employeeIds,
          fromDate: dates?.date,
          toDate: dates?.toDate,
        },
      });
      addSpans(res?.data);
    })();
  }, [employees, sections, addSpans, dates]);

  /**
   * TODO //
   * January hours are always zero
   * day hours fix
   * Duration changes by itself
   * after adding january it changes to december again
   * Percentage not working properly
   */
  useEffect(() => {
    (async () => {
      if (!id) return;
      const planning = await Service.search(
        "com.axelor.apps.project.db.ProjectPlanning",
        {
          fields: ["employeeSet"],
          data: {
            criteria: [
              {
                fieldName: "id",
                operator: "=",
                value: id,
              },
            ],
          },
        }
      );
      const employeeIds =
        planning?.data && planning.data[0]?.employeeSet?.map((u) => u.id);
      const res = await Service.search("com.axelor.apps.hr.db.Employee", {
        fields: [
          "fullName",
          "name",
          "user",
          "dailyWorkHours",
          "weeklyWorkHours",
        ],
        data: {
          criteria: [
            {
              fieldName: "id",
              operator: "IN",
              value: employeeIds,
            },
          ],
        },
      });
      const employees = res?.data;
      const userIds = employees?.map((emp) => emp?.user?.id);
      setEmployees(employees);
      const projects = await fetchProjects({
        fields: ["name", "membersUserSet", "fullName"],
        data: {
          criteria: [
            {
              fieldName: "membersUserSet.id",
              operator: "IN",
              value: userIds,
            },
            {
              fieldName: "projectStatus.isCompleted",
              operator: "=",
              value: false,
            },
          ],
          operator: "and",
        },
      });
      setProjects(projects);
      const sections = employees?.map((emp) => {
        return {
          ...emp,
          key: emp.id,
          label: emp.name,
          open: true,
          children: [
            ...((projects || []).filter((proj) =>
              (proj.membersUserSet || [])
                .map((u) => u.id)
                .includes(emp.user?.id)
            ) || []),
          ]?.map((p) => {
            return { ...p, key: `${emp.id}_${p.id}`, label: p.fullName };
          }),
        };
      });
      if (!sections) return;
      setSections(sections);
    })();
  }, [id]);

  return (
    <div style={{ width: "100%", height: "100%" }}>
      <Toolbar
        addSection={(section) => {
          addNewSection(section);
        }}
      />
      <>
        <EventFrom
          employees={employees}
          open={open}
          handleEvent={handleEvent}
          event={event}
          handleClose={handleClose}
          handleOk={handleOk}
          handleDelete={handleDelete}
        />
        <div id="scheduler_here" className="dhx_cal_container">
          <div className="dhx_cal_navline">
            <div className="dhx_cal_prev_button"></div>
            <div className="dhx_cal_next_button"></div>
            <div className="dhx_cal_today_button"></div>
            <div className="dhx_cal_date"></div>
            <div className="dhx_cal_tab" data-tab="halfday_timeline"></div>
            <div className="dhx_cal_tab" data-tab="day_timeline"></div>
            {/* <div className="dhx_cal_tab" data-tab="week_timeline"></div> */}
            <div className="dhx_cal_tab" data-tab="month_timeline"></div>
          </div>
          <div className="dhx_cal_header"></div>
          <div className="dhx_cal_data"></div>
        </div>
      </>
    </div>
  );
}
