import React from "react";
import TextField from "@mui/material/TextField";
import { makeStyles } from "@mui/styles";

import { fetchProjects } from "../../services/api";
import DatePicker from "../../components/DatePicker";
import Select from "../../components/Select";
import Dialog from "../../components/Dialog";
import Service from "../../services";

const useStyles = makeStyles({
  root: {
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",
    width: "100%",
    marginTop: 10,
  },
  widget: {
    marginBottom: "10px !important",
  },
});

export default function Scheduler({
  employees,
  open,
  handleEvent,
  event,
  handleClose,
  handleOk,
  handleDelete,
}) {
  const classes = useStyles();

  const fetchProducts = React.useCallback(async () => {
    const res = await Service.search("com.axelor.apps.base.db.Product", {
      fields: ["id", "fullName", "code"],
      data: {
        _domain: `self.isActivity = true AND self.dtype = 'Product'`,
        _domainContext: {
          _projectIds: [event?.project?.id],
        },
      },
    });
    return res?.data;
  }, [event]);

  const fetchTasks = React.useCallback(async () => {
    if (!event?.project) return [];
    const res = await Service.search("com.axelor.apps.project.db.ProjectTask", {
      data: {
        criteria: [
          {
            fieldName: "project.id",
            operator: "=",
            value: event?.project?.id,
          },
        ],
      },
    });
    return res?.data;
  }, [event]);

  const getProjects = React.useCallback(async () => {
    const projects = await fetchProjects({
      fields: ["name", "membersUserSet", "fullName"],
      data: {
        criteria: [
          {
            fieldName: "projectStatus.isCompleted",
            operator: "=",
            value: false,
          },
        ],
        operator: "and",
      },
    });
    return projects;
  }, []);

  return (
    <Dialog
      open={open}
      handleOk={handleOk}
      handleClose={handleClose}
      handleDelete={handleDelete}
      children={
        <div className={classes.root}>
          <TextField
            name="hours"
            className={classes.widget}
            placeholder="Planned hours per mode"
            type="number"
            size="small"
            value={event?.hours || 0}
            onChange={(e) => handleEvent("hours", e.target.value)}
          />
          <TextField
            name="plannedHours"
            className={classes.widget}
            placeholder="Planned hours"
            type="number"
            size="small"
            value={event?.plannedHours || 0}
            onChange={(e) => handleEvent("plannedHours", e.target.value)}
          />
          <Select
            name="project"
            label="Project"
            fetchAPI={getProjects}
            className={classes.widget}
            optionLabel="fullName"
            value={event?.project}
            onChange={(value) => handleEvent("project", value)}
          />
          {event?.project && (
            <Select
              name="projectTask"
              className={classes.widget}
              fetchAPI={fetchTasks}
              optionLabel="fullName"
              label="Task"
              value={event?.projectTask}
              onChange={(value) => handleEvent("projectTask", value)}
            />
          )}
          <Select
            name="product"
            label="Activity"
            className={classes.widget}
            fetchAPI={fetchProducts}
            optionLabel="fullName"
            value={event?.product}
            onChange={(value) => handleEvent("product", value)}
          />
          <Select
            name="employee"
            label="Employee"
            className={classes.widget}
            options={employees}
            value={event?.employee}
            optionLabel="name"
            onChange={(value) => handleEvent("employee", value)}
          />
          <DatePicker
            name="start_date"
            label="Date"
            className={classes.widget}
            value={event?.start_date}
            onChange={(value) => handleEvent("start_date", value)}
          />
          <Select
            name="startOnSelect"
            label="Start on"
            className={classes.widget}
            options={[
              {
                label: "Morning",
                value: 0,
              },
              { label: "Afternoon", value: 1 },
            ]}
            value={event?.startOnSelect}
            optionLabel="label"
            onChange={(value) => handleEvent("startOnSelect", value)}
          />
          <DatePicker
            name="end_date"
            label="To"
            className={classes.widget}
            value={event?.end_date}
            onChange={(value) => handleEvent("end_date", value)}
          />
          <Select
            name="endOnSelect"
            label="End on"
            className={classes.widget}
            options={[
              {
                label: "Morning",
                value: 0,
              },
              { label: "Afternoon", value: 1 },
            ]}
            value={event?.endOnSelect}
            optionLabel="label"
            onChange={(value) => handleEvent("endOnSelect", value)}
          />
          <TextField
            label="Description"
            multiline
            size="small"
            minRows={4}
            className={classes.widget}
            value={event?.description || ""}
            onChange={(e) => handleEvent("description", e.target.value)}
            name="description"
          />
        </div>
      }
    />
  );
}
