import * as React from "react";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import CircularProgress from "@mui/material/CircularProgress";

export default function Select({
  fetchAPI,
  value,
  onChange,
  name,
  optionLabel,
  multiple = false,
  label,
  className,
  options: propOptions,
}) {
  const [open, setOpen] = React.useState(false);
  const [options, setOptions] = React.useState([]);
  const loading = open && options.length === 0;

  React.useEffect(() => {
    let active = true;
    if (!loading) {
      return undefined;
    }
    if (propOptions) {
      setOptions(propOptions);
      return;
    }
    (async () => {
      const res = await fetchAPI();
      if (active) {
        setOptions([...res]);
      }
    })();
    return () => {
      active = false;
    };
  }, [loading, fetchAPI, propOptions]);

  React.useEffect(() => {
    if (!open) {
      setOptions([]);
    }
  }, [open]);

  return (
    <Autocomplete
      id="asynchronous-demo"
      multiple={multiple}
      sx={{ minWidth: 300 }}
      className={className}
      open={open}
      onOpen={() => {
        setOpen(true);
      }}
      onClose={() => {
        setOpen(false);
      }}
      options={options}
      loading={loading}
      name={name}
      value={value}
      onChange={(e, value) => {
        if (multiple) {
          let val = [
            ...(new Map(value.map((item) => [item["id"], item])).values() ||
              []),
          ];
          onChange(val);
        } else {
          onChange(value);
        }
      }}
      placeholder={label}
      size="small"
      getOptionLabel={(option) => option[optionLabel] || option}
      renderInput={(params) => (
        <TextField
          {...params}
          label={label}
          placeholder={label}
          size="small"
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <React.Fragment>
                {loading ? (
                  <CircularProgress color="inherit" size={20} />
                ) : null}
                {params.InputProps.endAdornment}
              </React.Fragment>
            ),
          }}
        />
      )}
    />
  );
}
